import counter from './counter.vue'
export default {
	components:{
		counter
	},
	data(){
		return{

		}},
	methods:{
		hideSpecifications(){
			//this.spaceInfo.showSpace = false
			this.$emit("hideSpec")
		},
		handleSpecification(item){
			//item就是数码 选中那个码
			//在ios 子级改变无效 要在父级里改
			//选择规格
			//子级注册事件 父级执行事件
			this.$emit("setSelectSpec",item)
		},
		sub(){
			//counter传过来 给goods用
			this.$emit("sub")
		},
		add(){
			this.$emit("add")
		}
	},
	props:{
		spaceInfo:Object,
		goodsInfo:Object,
		goodsData:Object
	}
}
